import React, { FC, useMemo, useRef, useState } from 'react';
import { IMessage } from '@src/api/Dialogs/models';
import * as ST from './styled';
import { getTimeMessage } from '@src/utils/date';
import { useAuth } from '@providers/AuthProvider';
import { Dropdown } from '@components/Select/Dropdown';
import { Button, ButtonVariant } from '@src/components';
import { Dialog, DialogTypes } from '@components/Dialog';
import { useModalControls } from '@src/hooks';
import { SocketApi } from '@src/api/SocketApi/SocketApi';
import { IComplaintMessage } from '@src/api/Complaints/models';

interface IProps {
  message: IMessage | IComplaintMessage;
}

export const Message: FC<IProps> = ({ message }) => {
  const { user } = useAuth();
  const refContainer = useRef<HTMLDivElement | null>(null);
  const deleteModal = useModalControls();
  const dropdown = useModalControls();

  const isOwn = useMemo(() => message.user.id === user?.id, [message, user]);

  const handleClick = (e: React.MouseEvent<HTMLDivElement>) => {
    if ('dialogId' in message) {
      e.preventDefault();
      if (isOwn) {
        dropdown.open();
      }
    }
  };

  const handleDelete = () => {
    if ('dialogId' in message) {
      SocketApi.deleteMessage({ messageId: message.id });
      deleteModal.close()
    }
  }

  return (
    <ST.Wrapper isOwn={isOwn}>
      <ST.Content ref={refContainer} isOwn={isOwn} onContextMenu={handleClick}>
        <ST.Text>{message.text}</ST.Text>
        <ST.Date>{getTimeMessage(message.updatedAt)}</ST.Date>
        {dropdown.isOpen && (
          <ST.StyledDropDown
            selectionContainer={refContainer.current as HTMLDivElement}
            onClose={dropdown.close}
          >
            <Button variant={ButtonVariant.DANGER} text="" fullWidth onClick={deleteModal.open} />
          </ST.StyledDropDown>
        )}
      </ST.Content>
      {deleteModal.isOpen && (
        <Dialog
          type={DialogTypes.WARNING}
          closeHandler={deleteModal.close}
          onConfirm={handleDelete}
          actions={{
            cancel: {
              text: '',
              onClick: deleteModal.close,
            },
            submit: {
              text: '',
            }
          }}
        >
              ?
        </Dialog>
      )}
    </ST.Wrapper>
  );
}
